.title {
  display: inline-block;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  padding: 2.25rem;
  margin-top: 3rem;

  .title-bar {
    position: relative;
    width: fit-content;
    margin: 0 auto;
    &::after {
      content: '';
      height: 0.75rem;
      background-color: $secondary-color;
      position: absolute;
      width: 100%;
      bottom: 0;
      left: 0;
    }
  }
  svg {
    width: 100%;
    transform: rotate(120deg);
    margin-top: 5rem;
    max-width: 300px;
  }
}

@media (min-width: 1080px) {
  .title {
    margin-top: 10rem;
    flex-direction: row;
    display: flex;
    padding: 0rem 6rem;
    justify-content: flex-start;
    align-items: center;

    .main {
      display: flex;
      flex-direction: column;
      text-align: start;
      align-items: flex-start;
      justify-content: flex-start;
      max-width: 580px;
    }
    svg {
      position: absolute;
      right: 5%;
      top: 20%;
      max-width: 436px;
    }
  }
}

@media (min-width: 1920px) {
  .title {
    margin-top: 12rem;
    flex-direction: row;
    display: flex;
    padding: 0rem 12.25rem;
    justify-content: flex-start;
    align-items: center;

    .main {
      display: flex;
      flex-direction: column;
      text-align: start;
      align-items: flex-start;
      justify-content: flex-start;
      max-width: 1080px;
    }
    svg {
      transform: rotate(0);
      position: absolute;
      right: 10%;
      top: auto;
      max-width: 736px;
    }
  }
}
