.container {
  background-color: $gray-background-color;
  display: flex;
  flex-direction: column;
  align-items: center;
  svg {
    width: 100%;
    height: 100%;
  }

  h2 {
    margin-top: 2rem;
    color: $purple-color;
  }

  &-blogs {
    display: grid;
    grid-template-columns: 1fr;
    margin: 4rem 1rem 0rem;

    .blog-card {
      max-width: 25rem;
      background-color: $white-background-color;
      border-radius: 1rem;
      margin-bottom: 2rem;
      height: fit-content;

      .header {
        max-width: 25rem;
        max-height: 15.625rem;
        border-radius: 1rem 1rem 0rem 0rem;
        overflow: hidden;
        img {
          max-width: 100%;
          max-height: 100%;
          border-radius: 1rem 1rem 0rem 0rem;
          object-fit: fill;
          transform-origin: center;
          transform-box: fill-box;
          transition: all 0.5s ease-in;
          &:hover {
            transform: rotate(2deg) scale(1.06) translateZ(0);
          }
        }
      }

      .body {
        min-height: 12.5rem;
        padding: 1rem 2rem;
        .blog-title {
          font-size: 1.125rem;
          font-weight: bold;
        }

        .date {
          font-size: 0.75rem;
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-top: 2rem;
          color: $text-gray-color;

          p {
            margin-left: 1rem;
            font-weight: 300;
          }
        }
        .tags {
          display: flex;
          margin-top: 1rem;
          flex-wrap: wrap;
        }
      }
    }
  }

  .btn-blogs {
    margin-top: 1rem;
    margin-bottom: 6rem;
  }
}

@media (min-width: 800px) {
  .container {
    &-blogs {
      grid-template-columns: 1fr 1fr;

      .blog-card {
        margin-right: 1rem;
      }
    }
    .btn-blogs {
      align-self: flex-end;
      margin-right: calc((100vw - 2 * 25rem) / 2);
      margin-top: 2.5rem;
    }
  }
}

@media (min-width: 1200px) {
  .container {
    margin-top: 2rem;
    &-blogs {
      grid-template-columns: 1fr 1fr 1fr;

      .blog-card {
        margin-right: 2rem;

        .body {
          .blog-title p {
            font-size: 1.125rem !important;
          }
          .date p {
            font-size: 1rem;
          }
        }
      }
    }
    .btn-blogs {
      margin-right: calc((100vw - 3 * 25rem) / 2);
      margin-top: 2rem;
      p {
        font-size: 1.5rem !important;
      }
    }
  }
}

@media (min-width: 1600px) {
  .container {
    &-blogs {
      .blog-card {
        .body {
          .blog-title p {
            font-size: 1.5rem !important;
          }
          .date p {
            font-size: 1.125rem;
          }
        }
      }
    }
  }
}
